.navbar{
    width: 100vw;
    height: 50px;
    background-color: rgba(3, 3, 3, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
    align-items: center;
    padding: 1rem;
    z-index: 1000;
    font-size: 1.5rem;
}
.navbar a {
    text-decoration: none;
    color: azure;
    font-size: 1.5rem;
    font-weight: bold;
    transition: all 0.3s ease;
}
.navbar a:hover {
    color: aqua;
}